	module.exports = {"data":{"markdownRemark":{"html":"<h1>代码块</h1>\n<ul>\n<li>使用 markdown 代码块时必须指认代码所用语言，用于自动化语言识别。</li>\n<li>代码块上下必须保留空行，保证与其他语法保持隔离。</li>\n</ul>\n<h1>留白（盘古之白）</h1>\n<h2>中英语言切换之间</h2>\n<p><strong>Good:</strong></p>\n<blockquote>\n<p>我是 Leon 的表弟，PHP 是世界上最好的语言。</p>\n</blockquote>\n<p><strong>Bad:</strong></p>\n<blockquote>\n<p>我是Leon本人，Java才是。</p>\n</blockquote>\n<hr>\n<h2>数字与单位之间</h2>\n<p><strong>Good:</strong></p>\n<blockquote>\n<p>这个方法的执行耗时 20 ms。</p>\n</blockquote>\n<p><strong>Bad:</strong></p>\n<blockquote>\n<p>这个方法执行时创建了10mb的执行空间。</p>\n</blockquote>\n<hr>\n<h2>中文链接之间</h2>\n<p>当然这里在语法渲染时已经经过了处理，但是依然建议在进行原文书写是保留空格提升个人书写时的体验。</p>\n<p><strong>Good:</strong></p>\n<blockquote>\n<p>这里是我的 <a href=\"http://jazzysnail.me\">个人博客</a> 欢迎往来。</p>\n</blockquote>\n<hr>\n<h2>在这些地方你不应该留白</h2>\n<ul>\n<li>以全角形式标点之前和之后没有额外的空格。</li>\n</ul>\n<h1>标点</h1>\n<h2>避免重复标点符号</h2>\n<p><strong>Good:</strong></p>\n<blockquote>\n<p>太棒啦！</p>\n</blockquote>\n<p><strong>Bad:</strong></p>\n<blockquote>\n<p>反杀？？？</p>\n</blockquote>\n<hr>\n<h2>中文文章使用全角标点</h2>\n<p><strong>Good:</strong></p>\n<blockquote>\n<p>我可爱么？喵~</p>\n</blockquote>\n<p><strong>Bad:</strong></p>\n<blockquote>\n<p>死基佬,滚!</p>\n</blockquote>\n<hr>\n<h2>英文整句使用半角标点</h2>\n<p><strong>Good:</strong></p>\n<blockquote>\n<p>The \"black mirror\" of the title is the one you'll find on every wall, on\nevery desk, in the palm of every hand: the cold, shiny screen of a TV, a\nmonitor, a smartphone.</p>\n</blockquote>\n<p><strong>Bad:</strong></p>\n<blockquote>\n<p>The ”black mirror“ of the title is the one you‘ll find on every wall，on\nevery desk，in the palm of every hand：the cold，shiny screen of a TV，a\nmonitor，a smartphone。</p>\n</blockquote>\n<h2>使用完整名词，避免行业语言</h2>\n<p><strong>Good:</strong></p>\n<blockquote>\n<p>我们需要一位熟悉 JavaScript、HTML5，至少理解一种框架（如 Vue、AngularJS、React 等）的前端开发者。</p>\n</blockquote>\n<p><strong>Bad:</strong></p>\n<blockquote>\n<p>我们需要一位熟悉 js、h5，至少理解一种框架（如 vue、angular、react 等）的前端开发者。</p>\n</blockquote>\n<h1>参考文献</h1>\n<ul>\n<li><a href=\"https://sparanoid.com/note/chinese-copywriting-guidelines/\">中文文案排版指北</a></li>\n</ul>","frontmatter":{"date":"March 19, 2018","path":"/format","title":"书写格式","author":"Leon","dsct":"这将使我们的文档更美观更易于阅读"}}},"pathContext":{}}

